@use "@styles/settings" as *;

.bl_regionTop {
  position: relative;
  padding: 80px 10px 60px;
  text-align: center;
  background-color: var(--color-white);

  @include breakpoint-up(md) {
    padding: 90px 20px;
  }


  &::before {
    content: "";
    position: absolute;
    inset: 37px 0 auto;
    margin-inline: auto;
    width: 30px;
    height: 30px;
    background-image:
      linear-gradient(to bottom,
        rgba(#4591CB, 0.6) 0, rgba(#4591CB, 0.6) 8px,
        rgba(#4591CB, 0.2) 8px, rgba(#4591CB, 0.2) 15px,
        #4591CB 15px, #4591CB 23px,
        rgba(#4591CB, 0.8) 23px, rgba(#4591CB, 0.8) 30px,
      );
    background-size: 30px 30px;
    background-repeat: no-repeat;
  }
}

.bl_regionTop_heading {
  h1 {
    font-size: rem(38);
    font-weight: 100;
    line-height: 1;
    letter-spacing: -0.04em;
    color: var(--color-02);

    @include breakpoint-up(md) {
      font-size: rem(64);
    }
  }

  p {
    margin-top: 10px;
    font-size: rem(18);
    font-weight: 600;
    color: var(--color-01);

    @include breakpoint-up(md) {
      font-size: rem(30);
    }
  }
}

.bl_regionTop_text {
  margin-top: 10px;
  font-size: rem(14);
  white-space: pre-line;

  @include breakpoint-up(md) {
    font-size: rem(16);
  }
}